.top-bar {
  width: 100%;
  background-color: #403F4C;
  color: #fff;
  margin-bottom: 1px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.top-bar-local {
  padding: 5px 20px;
  text-align: center;
  width: 100%;
}

.top-bar-error {
  background-color: #E84855;
  color: #fff;
  padding: 5px 20px;
  text-align: center;
  width: 100%;
}

.top-bar-duration {
  color: #F9DC5C;
  display: inline-block;
  margin-left: 10px;
}

@media (min-width: 600px) {

  .top-bar {
    -ms-flex-wrap: none;
        flex-wrap: none;
  }

  .top-bar-local {
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: left;
    width: auto;
  }

  .top-bar-error {
    text-align: right;
    width: auto;
  }

}


